<template>
  <v-app style="background: #111722">
    <v-main class="pt-5">
      <section id="Movie">
        <!-- <v-card color="#111722"> </v-card> -->
        <!-- START::__MOVIES_PRODUCTS__ -->
        <v-container>
          <v-row>
            <v-col>
              <div class="main_heading">
                <h1>
                  <span class="special_font">Popular Movies</span>
                </h1>
                <span class="special_design">
                  <i></i>
                  <i></i>
                  <i></i>
                </span>
              </div>
            </v-col>
          </v-row>
          <v-divider style="border-color: #8080802e"></v-divider>
          <!-- v-if="isImageInLoad" -->
          <v-row class="mb-12">
            <!-- START::__MOVIES_PRODUCTS__::CARD__ -->
            <v-col
              cols="6"
              sm="4"
              md="3"
              lg="2"
              v-for="(movie, i) in movies"
              :key="i"
              class="my-5 movie_row"
            >
              <v-hover v-slot="{ hover }" open-delay="50">
                <!-- { name: 'movieproduct', params: { item: movie.id } } -->
                <v-card
                  class="mx-auto"
                  @click="
                    saveToLocal(movie);
                    redirectTo(movie.id);
                  "
                  style="cursor: pointer"
                  max-width="200"
                >
                  <v-img
                    :min-height="C_img.height"
                    :min-width="C_img.width"
                    :src="`https://image.tmdb.org/t/p/w500${movie.poster_path}`"
                    @load="isImageLoaded = true"
                  >
                    <template #placeholder>
                      <v-card color="#111722">
                        <!-- <v-slide-x-transition> -->
                          <!-- type="image, list-item-three-line" -->
                        <v-skeleton-loader
                          type="image,image"
                          min-height="300"
                          :loading="true"
                          height="300"
                          v-bind="attrs"
                        />
                        <!-- </v-slide-x-transition> -->
                      </v-card>
                    </template>
                    <div
                      class="d-flex flex-column justify-space-between"
                      style="height: 100%"
                    >
                      <v-card-text
                        class="py-2 px-1 d-flex justify-space-between"
                      >
                        <h4
                          class="caption rating_box white--text font-weight-light px-4 py-0"
                        >
                          CAM
                        </h4>
                        <div
                          class="rating rating_box white--text font-weight-light px-2 py-0"
                        >
                          <v-icon left small color="#ffab31">mdi-star</v-icon>
                          <span class="caption">{{ movie.vote_average }}</span>
