<script>
import { Carousel3d, Slide } from "vue-carousel-3d";
import axios from "axios";
import { mapActions } from "vuex";
export default {
  data: () => ({
    page: 1,
    movies: [],
    dialog: true,
    loading: false,
    isImageInLoad: true,
    firstLoad: true,
    isImageLoaded: false,
    upcoming: [],
    attrs: {
      class: "mb-6",
      boilerplate: false,
      elevation: 2,
    },
    apiKey: "194d8a67379374e13e12c99420c19098",
    searchM: "",
    chips: [],
    example:
      "https://api.themoviedb.org/3/movie/550?api_key=194d8a67379374e13e12c99420c19098",
  }),
  methods: {
    ...mapActions(["A_enableDialog"]),
    redirectTo(movieId) {
      this.$router
        .push({ name: "movieproduct", params: { item: movieId } })
        .then(() => {
          this.$vuetify.goTo(0, {
            duration: 750,
          });
        })
        .catch((err) => err);
    },
    reload() {
      this.$nextTick(() => {
        this.isImageInLoad = false;
        setTimeout(() => {
          this.isImageInLoad = true;
        }, 0);
      });
    },
    saveToLocal(item) {
      sessionStorage.setItem("currentItem", JSON.stringify(item));
    },
    async getNewPage(isClicked) {
      this.$router.replace({ query: { page: this.page } }).catch((err) => err);
      await axios
        .get(
          `https://api.themoviedb.org/3/movie/popular?api_key=${this.apiKey}&language=en-US&page=${this.page}`
        )
        .then((resp) => {
          this.movies = resp.data.results;
          setTimeout(() => {
            window.scrollTo({
              behavior: "smooth",
              top: 0,
            });
            // this.checkIfUpdated();
          }, 250);
        })
        .catch((err) => err);
    },
    checkIfUpdated() {
      const readyHandler = () => {
        if (document.readyState == "complete") {
          // console.log("movies updated completed");
          setTimeout(() => {
            // this.firstLoad = false;
          }, 1500);
          document.removeEventListener("readystatechange", readyHandler);
        }
      };
      document.addEventListener("readystatechange", readyHandler);
      readyHandler();
    },
  },
  async mounted() {
    this.page = Number(this.$route.query.page) || 1
    this.getNewPage(false);
    await axios
      .get(
        `https://api.themoviedb.org/3/genre/movie/list?api_key=${this.apiKey}&language=en-US`
      )
      .then((resp) => {
        this.chips = resp.data.genres;
      })
      .catch((err) => err);
    await axios
      .get(
        `https://api.themoviedb.org/3/movie/upcoming?api_key=${this.apiKey}&language=en-US&page=1`
      )
      .then((resp) => {
        this.upcoming = resp.data.results;
      })
      .catch((err) => err);
  },
  computed: {
    posterPath(movie) {
      return "https://image.tmdb.org/t/p/w500" + movie.poster_path;
    },
    C_img() {
      return this.$vuetify.breakpoint.lgAndUp
        ? { width: 173, height: 300 }
        : this.$vuetify.breakpoint.mdOnly
        ? { width: 200, height: 300 }
        : this.$vuetify.breakpoint.mdOnly
        ? { width: 175, height: 260 }
        : { width: 155, height: 230 };
    },
  },
  created() {
    const readyHandler = () => {
      if (document.readyState == "complete") {
        // console.log("movies complete");
        setTimeout(() => {
          this.firstLoad = false;
        }, 1500);
        document.removeEventListener("readystatechange", readyHandler);
      }
    };
    document.addEventListener("readystatechange", readyHandler);
    readyHandler();
  },
  /* eslint-disable vue/no-unused-components */
  /* eslint-disable vue/no-async-in-computed-properties */

  components: {
    Carousel3d,
    Slide,
  },
};
</script>

